.ticker {
    list-style-type: none;
    min-width: 250px;
    padding-left: 0;
    font-weight: bold;
}


.ticker__coin {
    display: flex;
    justify-content: space-between;
}

.coin__name {
    min-width: 50px;
    color: var(--color-computer-green);
}

.coin__value {
    min-width: 100px;
    text-align: left;
    color: var(--color-computer-green);
    font-weight: 400;
}

.coin__change {
    min-width: 50px;
    text-align: right;
}

.coin__change--red {
    color: var(--color-computer-red);
}

.coin__change--green {
    color: var(--color-coin-green);
}

#ticker-change-hr {
    font-size: 0.6rem;
    font-weight: 400;
    color: grey;
    text-transform: lowercase;
    letter-spacing: 1px;
}

.ticker__buy-container {
    display: flex;
    justify-content: space-between;
    margin-top: 0.5rem;
    margin-bottom: 1rem;
}

.ticker__input {
    width: 80%;
    border: none;
    border-radius: 5px 0 0 5px;
    height: 2rem;
    text-align: right;
    padding-right: 0.5rem;
    background-color: var(--color-primary-0);
}

.ticker__buy-btn {
    border: none;
    width: 20%;
    border-radius: 0 5px 5px 0;
}



.inventory {
    list-style-type: none;
    min-width: 250px;
    padding-left: 0;
    font-weight: bold;
}

.inventory__coin {
    display: flex;
    justify-content: space-between;
}

.coin__name {
    min-width: 50px;
    color: var(--color-computer-green);
}

.coin__value {
    min-width: 100px;
    text-align: left;
    color: var(--color-computer-green);
    font-weight: 400;
}

.coin__change {
    min-width: 50px;
    text-align: right;
}

.coin__change--red {
    color: var(--color-computer-red);
}

.coin__change--green {
    color: var(--color-coin-green);
}

#inventory-change-hr {
    font-size: 0.6rem;
    font-weight: 400;
    color: grey;
    text-transform: lowercase;
    letter-spacing: 1px;
}

.coin__value--fiat {
    min-width: 100px;
    text-align: left;
    color: var(--color-coin-altgreen);
    font-weight: 400;
}