.social-container {
    overflow-y: scroll;
    background-color: var(--color-primary-4);
    opacity: 1;
    border-radius: 0.25rem;
    height: 85%;
}

/* width */
::-webkit-scrollbar {
    width: 10px;
}
  
/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: var(--color-secondary-1-2);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}

.event-log {
    padding: 0.5rem 1rem;
    margin-bottom: 1rem;
    width: 80vw;
    text-transform: none;
    letter-spacing: 0;
    font-family: monospace;
    font-size: 0.7rem;
    color: var(--color-primary-0b);
}

.event-log__date {
    color: var(--color-computer-green);
    display: inline;
    font-size: 0.8rem;
}

.event-log__log {
    margin: 0.5rem auto 0;
}

.event-log__delete {
    padding: 0.2rem;
    margin-left: 0.5rem;
    font-size: 0.7rem;
    font-weight: bold;
    color: var(--color-secondary-2-1);
    border: 1px solid var(--color-secondary-2-1);
}

.event-log__delete:hover {
    filter: brightness(50%)
}

.event-log__hr-bar {
    /* height: 0.5rem; */
    margin: 0 auto 1rem;
    width: 50%;
    border: 1px solid var(--color-computer-green);
    opacity: 0.3;
}